@import '../../index.scss';

.material-icons {
  position: relative;
  top: 0.35rem;
  margin-right: 0.5rem;
}

.tasklist {
  cursor: pointer;
  .finished {
    text-decoration: line-through;
    opacity: 50%;
  }
}

.container-button {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 1.5rem;
  .add-task {
    cursor: pointer;
    color: $primary-color;
    background: white;
    border: 0;
    font-size: 4.5rem;
  }
}
