@import '../../index.scss';

.nav-wrapper {
  .material-icons {
    font-size: 3rem;
    padding-left: 1.5rem;
  }
}

.sidenav {
  button {
    width: 100%;
    text-align: left !important;
  }
}

.sidebar-container {
  width: 80%;
  height: 100vh;
  box-shadow: 12px 0 15px -4px rgba(31, 73, 125, 0.8);
}

.sidebar-top-bar {
  height: 5.5rem;
  font-size: 2rem;
  background-color: $primary-color;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
}

.sidebar-avatar {
  width: 3rem;
}

.sidebar-share {
  i {
    margin-right: 0.5rem;
  }
}

.sidebar-options-header {
  margin-left: 1rem;
  font-weight: bold;
  i {
    margin-right: 0.5rem;
  }
}

.collection {
  cursor: pointer;
  .collection-item:hover {
    background-color: $secondary-color;
  }
  .collection-item {
    padding-left: 4rem !important;
  }
}
.modal {
  width: 100em;
}

.row {
  .logout {
    border-radius: 5%;
    bottom: -17rem;
    position: relative;
    background-color: $secondary-color;
    width: 40%;
    .btn {
      padding: 10px;
      height: fit-content;
    }
  }
}
